import $ from 'jquery';
import slick from 'slick-carousel';

$(function() {
    $('.holder__slider').slick({
        infinite: true,
        fade: true,
        prevArrow: '<img class="slider__arrows slider__arrows-left" src="../../public/img/decor/arrow-left.svg" alt=""></img>',
        nextArrow: '<img class="slider__arrows slider__arrows-right" src="../../public/img/decor/arrow-right.svg" alt=""></img>',
    });

});


